.services {
  padding: 40px 0;
  @include for-big {
    padding: 4rem 0;
  }
  @include small-tablet {
    padding: 20px 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px 20px;

    margin-bottom: 30px;
    @include for-big {
      gap: 3rem 2rem;
      margin-bottom: 3rem;
    }
    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__more {
    text-align: center;
  }

  &__btn {
    min-width: 334px;
    @include for-big {
      min-width: 33.4rem;
    }
  }
}

.services-item {
  position: relative;
  background: linear-gradient(135deg, #e9f2fd 0%, #f9f1ee 100%);
  border: 1px solid var(--seraya-obvodka);
  border-radius: 20px;
  padding: 25px;
  padding-top: 106px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  @include for-big {
    border-radius: 2rem;
    padding: 2.5rem;
    padding-top: 10.6rem;
    gap: 4rem;
  }
  @include small-tablet {
    border-radius: 10px;
    padding: 15px;
    padding-top: 40px;
  }
  &__icon {
    height: 137px;
    aspect-ratio: 1 / 1;
    @include for-big {
      height: 13.7rem;
    }
    @include small-tablet {
      height: 100px;
    }
    @include mobile {
      height: 71px;
    }
    svg {
      height: 100%;
      object-fit: contain;
    }
  }

  &__name {
    display: block;
    font-weight: 500;
    font-size: 25px;
    text-align: center;
    color: var(--text-color);
    @include for-big {
      font-size: 2.5rem;
    }
    @include small-tablet {
      font-size: 20px;
    }
    @include mobile {
      font-size: 14px;
    }
  }

  &__link {
    position: absolute;
    z-index: 1;
    right: 20px;
    top: 20px;
    width: 53px;
    height: 53px;
    transition: transform 0.3s ease;
    @include small-tablet {
      width: 35px;
      height: 35px;
      right: 10px;
      top: 10px;
    }
    @include mobile {
      width: 25px;
      height: 25px;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }

  @include hover {
    .services-item__link {
      transform: rotate(45deg);
    }
  }
}
