.consult-steps {
  padding: 40px 0;
  @include for-big {
    padding: 4rem 0;
  }
  @include small-tablet {
    padding: 20px 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;

    counter-reset: counter 0;
    @include for-big {
      gap: 2rem;
    }
    @media (max-width: 900px) {
      grid-template-columns: 1fr;
      gap: 10px;
    }
  }

  &__step {
    counter-increment: counter 1;
  }
}

.step {
  padding: 28px;
  border: 1px solid var(--seraya-obvodka);
  border-radius: 20px;
  background: linear-gradient(135deg, #e9f2fd 0%, #f9f1ee 100%);
  @include for-big {
    padding: 2.8rem;
    border-radius: 2rem;
  }
  @include mobile {
    padding: 10px;
    border-radius: 10px;
  }
  &__header {
    margin-bottom: 20px;
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    align-items: baseline;
    gap: 10px;
    padding: 10px 20px;
    @include for-big {
      margin-bottom: 2rem;
      border-radius: 1rem;
      gap: 1rem;
      padding: 1rem 2rem;
    }
    @include mobile {
      padding: 10px;
      margin-bottom: 10px;
    }
    &::before {
      content: counter(counter);
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 40px;
      text-align: center;
      color: var(--text-color);
      @include for-big {
        font-size: 4rem;
      }
      @include small-tablet {
        font-size: 26px;
      }
      @include mobile {
        font-size: 22px;
      }
    }
  }

  &--thin {
    .step__header {
      padding: 10px;
      margin-bottom: 10px;
      @include for-big {
        padding: 1rem;
        margin-bottom: 1rem;
      }
      @media (max-width: 900px) {
        padding: 20px 10px;
      }
    }
    .step__header::before {
      font-size: 25px;
      @include for-big {
        font-size: 2.8rem;
      }
    }
  }
}
